import React from "react";
import { Grid, Container } from "@material-ui/core";
import { styled } from "@material-ui/core/styles";

import Carousel from "./MyCarousel";
import FlowerTitle from "./FlowerTitle";
import "./Flower.css";

const FlowerItem = (props) => {
    const { data } = props;

    return (
        <MyFlower item className="container">
            <Carousel>
                <img
                    alt="flowers"
                    src={data.imgList[0]}
                    style={{ width: "100%", height: "265px" }}
                />
            </Carousel>
            <div className="bottomContainer">
                <p className="name">Зузаахай</p>
                <FlowerTitle />
            </div>
        </MyFlower>
    );
};

export default FlowerItem;

const MyFlower = styled(Grid)({
    display: "flex",
    flexDirection: "column",
    width: "200px",
    height: "350px",
    justifyContent: "center",
    alignItems: "center",
    margin: "20px 10px",
});
