import React from "react";
import { Grid, Button } from "@material-ui/core";
import wow1 from "../../../assets/Images/wow1.jpg";
import wow2 from "../../../assets/Images/wow2.jpg";
import gadna from "../../../assets/Images/gadna.png";
import dotor from "../../../assets/Images/dotor.png";

import "./css/inOut.css";

function InOut() {
    return (
        <Grid container>
            <Grid item xs={12} sm={6}>
                <div
                    style={{
                        backgroundImage: `url(${wow2})`,
                    }}
                    className="image"
                />
            </Grid>
            <Grid item xs={12} sm={6}>
                <Grid container className="firstImageDescription">
                    <Grid
                        item
                        xs={9}
                        sm={8}
                        md={7}
                        lg={6}
                        className="firstImageDescriptionContainer"
                    >
                        <img
                            alt="gadna"
                            src={gadna}
                            className="imageDescriptionImage"
                        ></img>
                        <Button
                            variant="outlined"
                            className="descriptionButton"
                        >
                            Дэлгэрэнгүй
                        </Button>
                    </Grid>
                    <Grid item xs={3} sm={4} md={5} lg={6}></Grid>
                </Grid>
            </Grid>
            <Grid item xs={12} sm={6}>
                <Grid container className="secondImageDescription">
                    <Grid item xs={3} sm={4} md={5} lg={6}></Grid>
                    <Grid
                        item
                        xs={9}
                        sm={8}
                        md={7}
                        lg={6}
                        className="secondImageDescriptionContainer"
                    >
                        <img
                            alt="dotor"
                            src={dotor}
                            className="imageDescriptionImage"
                        ></img>
                        <Button
                            variant="outlined"
                            className="descriptionButton"
                        >
                            Дэлгэрэнгүй
                        </Button>
                    </Grid>
                </Grid>
            </Grid>
            <Grid item xs={12} sm={6}>
                <div
                    style={{
                        backgroundImage: `url(${wow1})`,
                    }}
                    className="image"
                />
            </Grid>
        </Grid>
    );
}

export default InOut;
